import Vue from 'vue'
import Router from 'vue-router'
import Layout from './components/Layout.vue'

Vue.use(Router)

export default new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '',
      component: Layout,
      children: [
        {
          path: "/",
          name: "index",
          component: () => import("@/views/Qa.vue")
        },
        {
          path: "/qa",
          name: "qa",
          component: () => import("@/views/Qa.vue")
        }
      ]
    },
    {
      path: "/preview",

      name: "preview",
      component: () => import("@/views/Preview.vue")
    }
  ]
})
